.page {
    position: absolute;
    top: 0;
    height: 100vh;
    background-color: #f1f1f1;
    width: 100%
}

.App {
    width: 100%;
    height: 100vh;
}

.nag {
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    color: #a1a1a1;
    margin: 0;
    padding: 0;
    background-color: #fff;

}

.img {
    width: 100%;
}

.nag > li {
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 14px;
    padding: 10px 0;
    margin: 0 30px;
    text-align: center;
}

.navSelected {
    color: #5086ec;
    border-bottom: #5086ec 3px solid;

}

.page2 {
    width: 100%;
    height: 100%;
    background-color: #f1f1f1;
}

.page3 {
    padding: 0 20px;
    margin-top: 20px;
}

.page3 p {
    margin: 0;
}

.page3 > li {
    font-size: 14px;
    /*color: #252525;*/
    margin: 0 0 10px 0;
    padding: 0 0 5px 0;
    border-bottom: 1px solid #cdcdcd;
    list-style: none;
}

.page3 > li > p:nth-child(1) {
    margin-bottom: 10px;
}

.page3 > li > p {
    padding: 0 5px;
}

.page3 > li > span {
    padding: 0 5px;
}

.name {
    margin-right: 10px;
}

.page3 > li > .time {
    font-size: 8px;
    margin-top: 4px;
}

.spot {
    vertical-align: middle;
    margin-right: 5px;
    display: inline-block;
    width: 4px;
    height: 4px;
    background-color: #000;
    border-radius: 2px;
}

.title {
    margin: 10px 20px;
    background-color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    font-size: 8px;
    font-weight: 500;
    -ms-flex-align: center;
        align-items: center;
    padding: 5px 0;
    line-height: 12px;
}

.title > div {
}

.title > img {
    width: 20px;
    margin: 10px;
    height: 18px;
}

.page1 {
    background-color: #f1f1f1;
}

.page1 > ul {
    margin: 0;
    padding: 0;
    height: 100%;
    background-color: #f1f1f1;
    overflow: auto;
}

.page1 > ul > li {
    list-style: none;
}

.type {
    color: #a1a1a1;
    font-size: 13px;
    padding-left: 20px;
}

.content {
    background-color: #fff;
    padding: 10px 20px;
    margin-top: 10px;
    margin-bottom: 10px;
    color: #626262;
    /*font-size: 14px;*/
    min-height: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
}

.btnContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    background-color: #fff;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 40px;
}

button {
    width: 160px;
    height: 35px;
    color: #5086ec;
    border: #5086ec 1px solid;
    border-radius: 8px;
    background-color: #fff;
}

button:nth-child(2) {
    background-color: #5086ec;
    color: white;
}

.spotN {
    border-radius: 7px;
    width: 12px;
    height: 12px;
    border: 1px solid #5086ec;
    margin-right: 4px;
}

.spotS {
    border-radius: 7px;
    width: 12px;
    height: 12px;
    border: 1px solid #5086ec;
    margin-right: 4px;
}

.spotS div {
    background-color: #5086ec;
    border-radius: 5px;
    width: 10px;
    height: 10px;
    margin: 1px;
}

.contentTime {
    border-radius: 4px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: start;
    margin: 10px;
    /*padding: 10px 10px;*/
}

.outTime {
    margin-bottom: 15px;
    margin-top: 5px;
}

.pageCodeContent {
    margin: 10px;
    background-color: #fff;
    border-radius: 8px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px;
}

.pageCodeContent:nth-last-child {
    -ms-flex-align: start;
        align-items: start;
}

.local {
    font-weight: bold;
    font-size: 18px;
    height: 30px;
    line-height: 20px;

}

.local2 {
    color: #1a4089;
    font-size: 14px;
    border-top: 1px solid #dddddd;
    width: 100%;
    text-align: center;
    height: 30px;
    line-height: 40px;
}

.pageCode {
    background-color: #f1f1f1;
    height: 100%;
}

.pageCodeContentCity {
    font-size: 8px;
    color: #a1a1a1;
    -ms-flex-align: start;
        align-items: start;
}

.pageCodeContent > img {
    width: 140px;
    height: 140px;
    margin-top: 20px;
}

.code {
    margin-top: -30px;
}

.tip {
    font-weight: bold;
    text-align: center;
    position: absolute;
    top: -8px;
    left: 16%;
    padding: 0 10px;
    background-color: #f1f1f1;
}

h4 {
    font-size: 16px;
    margin-top: 0;
}

.scanTime {
    color: #1a4089;
    font-size: 14px;
}

.bgc {
    background-color: #1a4089;
    color: #ffffff;
    padding: 0 15px 40px;
}

.bgc h1 {
    padding: 10px;
    margin: 0;
    text-align: center;
    font-size: 20px;
}

.bgc img {
    background-color: #fff;
    padding: 5px;
    height: 30px;
    width: 30px;
    margin-right: 10px;
    border-radius: 20px;
    vertical-align: middle;

}

.big {
    font-size: 14px;
    margin-left: 10px;
    font-weight: bold;
    vertical-align: middle;
}

.small {
    font-weight: 300;
    vertical-align: middle;
}



